.sec6 {
	background-color: $gray-main-darken;

	&__note {
		transform: translate(-50%,-70%);
		position: absolute;
		top: 0;
		left: 50%;
	}

	&__note-title {
		@include hor-lines($width: 85px, $side-padding: 10px);

		@include media(w768) {
			@include hor-lines($width: 35px, $side-padding: 10px);
		}
	}

	&__note-border {
		fill: $gray-main-darken;
	}

	&__map {
		height: 740px;

		@include media(w1024) {
			height: 70vw;
		}
	}

	&__map-popup {
		user-select: none;
		color: white;
		text-align: center;
		font-size: 16px;
		height: 60px;
		width: 200px;

		&:before {
			content: "";
			display: inline-block;
			vertical-align: middle;
			height: 100%;
			width: 0;
		}

		div {
			display: inline-block;
			vertical-align: middle;
		}

		p {
			margin-bottom: 0;
		}
	}

	&__map-popup-hide {
		display: none;
	}
}